import { Component, OnInit } from '@angular/core';
import { TZB_CUSTOMER_VIDEOS, } from "app/@uisftech/common/service/http.constant";
import { DomSanitizer } from "@angular/platform-browser";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CustomSearchHttpService } from "app/pages/tzb/custom/http/custom-search/custom-search.http.service";
import { Message } from "primeng/primeng";

@Component({
  selector: 'app-customer-videos',
  templateUrl: './customer-videos.component.html',
  styleUrls: ['./customer-videos.component.scss'],
  providers: []
})
export class customerVideosComponent implements OnInit {
  srcUrl: any; // 引入iframesrc 加入参
  showIndex: number = 0;// 默认展示菜单列表中的第一条 客户资料 
  menuList: any = [
    { label: '客户资料' },
    { label: '存贷款挂接' },
    { label: '客户凭证接收' },
    { label: '代缴费签约' },
    { label: '信用卡业务' },
    { label: '网银签约' },
    { label: '方付通签约' },
    { label: '智多薪签约' },
    { label: '综合交易' },
    { label: '现场签约' },
    { label: '工作日程' },
    { label: '客户信息修改申请' },
    { label: '征信授权' },
    { label: '现场调查信息' },
    { label: '影像补录' },
  ];  // 菜单列表
  msgs: Message[] = [];


  TAB_TYPE = '02';
  QueryFlag = 1
  UID = 'MOBILE'
  PWD = 'cyg'
  AppID = 'SF'
  OrgOwner = 'XD'
  SERIAL_TYPE: '贷前'
  OBJECTNAME: 'APP1009'
  Post;// 任职岗位
  UserID;// 登陆人工号
  UserName;// 登陆人姓名
  OrgID; // 登陆人所属机构ID
  OrgName;// 登陆人所属机构名称
  seqList;  // esb返回流水list
  flexibleValue; // 动态循环的流水参数

  constructor(
    private sanitizer: DomSanitizer,
    public commfunc: Commfunc,
    private httpService: CustomSearchHttpService,
  ) {


  }

  ngOnInit() {
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    let chName = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    this.Post = commonHeader.extParam.postId;     // 任职岗位
    this.UserID = commonHeader.userId;     // 登陆人工号
    this.UserName = chName.userName;     // 登陆人姓名
    this.OrgID = commonHeader.orgId;     // 登陆人所属机构ID
    this.OrgName = chName.orgName;       // 登陆人所属机构名称
    this.selectSerial(this.menuList[0], 0);//默认查询第一行菜单
  }

  // 查看影像系统
  selectSerial(item, i) {
    if (i == 12) {  // 征信授权
      this.TAB_TYPE = '50'
    } else if (i == 13) { // 现场调查信息
      this.TAB_TYPE = '18'
    } else if (i == 14) { // 影像补录
      this.TAB_TYPE = '18';
      this.QueryFlag = 2;
    }
    this.showIndex = i; // ngClass类加给被点击的菜单
    this.flexibleValue = '';

    let fixedValue = '?UID=' + this.UID + '&PWD=' + this.PWD + '&AppID=' + this.AppID + '&UserID=' + this.UserID + '&UserName=' + this.UserName + '&OrgID='+ this.OrgID + '&OrgName=' + this.OrgName + '&OrgOwner=' + this.OrgOwner + '&QueryFlag=' + this.QueryFlag + '&Post=' + this.Post;

    let custNo = JSON.parse(this.commfunc.getSessionData('custNo'));
    const param = {
      clientNo: custNo
    };
    this.httpService.BZ_1100300008103_MP(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.seqList = data.infoList;
        this.seqList.forEach((el, index) => {
          let num = index + 1;
          this.flexibleValue = this.flexibleValue + '&info' + num + '=TABSORDER:' + num + ';BUSI_SERIAL_NO:IMP' + el.clientNo + ';TABNAME:'+ item.label + num + ';PROSERIALNO:' + el.impSeqNo + ';OBJECTNAME:APP1009;SERIAL_TYPE:贷前;TAB_TYPE:'+ this.TAB_TYPE + ';'
        });
        let reansportValue = TZB_CUSTOMER_VIDEOS + fixedValue + this.flexibleValue;
        console.log(reansportValue)
        this.srcUrl = this.sanitizer.bypassSecurityTrustResourceUrl(reansportValue);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }

}